.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.logo {
  height: 32px;
  margin: 16px;
}
.logo h1 {
  font-size: 25px;

  color: white;
}

.ant-layout-sider-children {
  height: 100vh;
}

:where(.css-dev-only-do-not-override-1fviqcj).ant-layout
  .ant-layout-sider-children {
  height: 100vh;
  margin-top: -0.1px;
  padding-top: 0.1px;
}

.circle {
  display: inline-block;
  background-color: #00d53b;
  border-radius: 50%;
  height: 40px;
  width: 50px;
}

.circle-inner {
  color: white;
  text-align: center;
  font-size: 20px;
  margin-top: 6px;
}
